.chat-list{max-height:80vh;margin-top:1em;padding-right:1em;display:flex;flex-direction:column;grid-gap:10px;gap:10px;overflow-y:scroll}.chat-list:hover a::after{opacity:1}.chat-list a{position:relative;display:flex;align-items:center;justify-content:center;border-radius:6px;text-decoration:none;background-color:rgba(255,255,255,.1);z-index:1}.chat-list a.unread .card-content .chat-info .display-name,.chat-list a.unread .card-content .chat-info .last-chat{font-weight:bold;color:#d4d4d4}.chat-list a:hover::before{opacity:1}.chat-list a::before,.chat-list a::after{content:"";width:100%;border-radius:inherit;height:100%;left:0;opacity:0;position:absolute;top:0;transition:opacity 500ms}.chat-list a::before{background:radial-gradient(800px circle at var(--mouse-x) var(--mouse-y), rgba(255, 255, 255, 0.06), transparent 40%);z-index:3}.chat-list a::after{background:radial-gradient(150px circle at var(--mouse-x) var(--mouse-y), rgba(255, 255, 255, 0.4), transparent 40%);z-index:1}.chat-list a .card-content{width:calc(100% - 2px);height:calc(100% - 2px);display:flex;align-items:center;grid-gap:10px;gap:10px;padding:16px;border-radius:inherit;margin:1px;background-color:#181717;background-color:#121212;color:inherit;z-index:2}.chat-list a .card-content .avatar-container{position:relative;display:flex;justify-content:center;align-items:center;overflow:hidden}.chat-list a .card-content .avatar-container .avatar{width:40px;height:40px;margin-right:10px;z-index:0}.chat-list a .card-content .avatar-container .online-user{width:16px;height:16px;position:absolute;top:-2px;right:8px;display:flex;justify-content:center;align-items:center;border-radius:50%;background-color:#1b1a1a;z-index:10}.chat-list a .card-content .avatar-container .online-user .status-circle{width:10px;height:10px;border-radius:50%;background-color:#0ebb50}.chat-list a .card-content .chat-info{display:flex;flex-direction:column;justify-content:center}.chat-list a .card-content .chat-info .display-name{font-weight: