.user-list{min-width:360px;min-height:100vh;display:flex;flex-direction:column;box-sizing:border-box;background-color:#1b1a1a;color:#d4d4d4;overflow-y:auto;z-index:100}.user-list h2{padding-bottom:10px;font-size:2rem}.user-list .search{position:relative;padding:0 1em}.user-list .search .search-form label{width:100%;display:flex;align-items:center;grid-gap:1em;gap:1em;padding:12px;margin:2em 0;border:1px solid #313131;border-radius:8px;font-size:1.5rem;background-color:#121212;color:#777;outline:none}.user-list .search .search-form label input{all:unset;cursor:text;flex-grow:1}.user-list .search .add-users .user-chat{display:flex;align-items:center;grid-gap:10px;gap:10px;padding:.5em;border-radius:5px;cursor:pointer;color:#fff}.user-list .search .add-users .user-chat:hover{background-color:#643e7a}.user-list .search .add-users .user-chat img{width:35px;height:35px;border-radius:50%;object-fit:cover}.user-list .search .add-users .user-chat .user-chat-info span{font-size:1.6rem}.user-list .search .add-users .user-chat .user-chat-info p{font-size:1.4rem;color:#d3d3d3}.user-list .user-list-item{display:flex;justify-content:flex-start;align-items:center;padding:.5em .3em;border-radius:4px}.user-list .user-list-item:hover{cursor:pointer;background-color:#242323}.user-list .user-list-item .user-name-bio{display:flex;flex-direction:column}.user-list .user-list-item .user-name-bio .user-display-name{font-size:1.6rem}.user-list .user-list-item .user-name-bio .long-username{color:#8b8b8b}.user-list .user-list-item .user-name-bio .user-bio{font-size:.8rem;color:#888a90}.user-list .user{display:flex;align-items:center;justify-content:space-between;padding:1em;margin-top:auto;margin-bottom:0;border-top:1px solid rgba(255,255,255,.2)}.user-list .user div{display:flex;align-items:center}.user-list .user div .avatar{width:35px;height:35px;margin-right:10px}
